import React, { useState, useEffect } from 'react';

import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import Tooltip from "react-bootstrap/Tooltip";


import data from '../assets/data/doggo.json'

function Breed(props) {
    
    let fullname = props.data.name_fr_x ;
    let name = props.data.name_fr_x ;
    if (name.length > 25 ){
        name = name.substring(0, 22) + "(...)"
    }

    return (
        <>
      
        <Col md={2}>
            <Card className="mt-4">
                <Card.Img variant="top" src={"http://cedricmaigrot.fr/fci/"+props.data.numeroFCI+".jpg"} />
                <Card.Body>
                    <Card.Title>
                        <OverlayTrigger placement="top" overlay={
                                <Tooltip id={`tooltip-${name}`}>
                                Nom complet : <strong>{fullname}</strong>.
                                </Tooltip>
                            }>
                            <span>{name}</span>
                        </OverlayTrigger>
                    </Card.Title>
                    </Card.Body>
                <ListGroup variant="flush">
                    <ListGroup.Item>Numro FCI : {props.data.numeroFCI}</ListGroup.Item>
                    <ListGroup.Item>Poids : {props.data.weight} kg</ListGroup.Item>
                    <ListGroup.Item>Taille : {props.data.height} cm</ListGroup.Item>
                </ListGroup>
                <Card.Footer>
                    {props.data.groupe} / Section <i>{props.data.section}</i>
                </Card.Footer>
            </Card>
        </Col>
        </>
    );
}
    
export default Breed;